import React from "react";

export const Snow = () => {
    
    const makeSnow = () => {
        let snow = [];
        for (var i = 0; i < 201; i++)
        {
            snow.push({ snowElement: <div className="snow" key={i}></div> })
        }

        return snow;
    }

    return (
        <div className="snowflakes">
            {makeSnow().map(snow => snow.snowElement)}
        </div>
    );
};
